import os = require('os');
import nodestacktrace = require('stack-trace');
import path = require('path');
import https = require('https');
import url = require('url');
                                                                                                                                                                                                                                       

export class NodeEnvironmentInfoCollector implements IEnvironmentInfoCollector {
  public getEnvironmentInfo(context: EventPluginContext): IEnvironmentInfo {
    function getIpAddresses(): string {
      let ips: string[] = [];
      let interfaces = os.networkInterfaces();
      Object.keys(interfaces).forEach((name) => {
        interfaces[name].forEach((iface: any) => {
          if ('IPv4' === iface.family && !iface.internal) {
            ips.push(iface.address);
          }
        });
      });

      return ips.join(', ');
    }

    if (!os) {
      return null;
    }

    let environmentInfo: IEnvironmentInfo = {
      processor_count: os.cpus().length,
      total_physical_memory: os.totalmem(),
      available_physical_memory: os.freemem(),
      command_line: process.argv.join(' '),
      process_name: (process.title || '').replace(/[\uE000-\uF8FF]/g, ''),
      process_id: process.pid + '',
      process_memory_size: process.memoryUsage().heapTotal,
      // thread_id: '',
      architecture: os.arch(),
      o_s_name: os.type(),
      o_s_version: os.release(),
      ip_address: getIpAddresses(),
      machine_name: os.hostname(),
      // install_id: '',
      runtime_version: process.version,
      data: {
        loadavg: os.loadavg(),
        platform: os.platform(),
        tmpdir: os.tmpdir(),
        uptime: os.uptime()
      }
    };

    if ((<any>os).endianness) {
      environmentInfo.data.endianness = (<any>os).endianness();
    }

    return environmentInfo;
  }
}

                                                                                                                                                                                                                                                                   

export class NodeErrorParser implements IErrorParser {
  public parse(context: EventPluginContext, exception: Error): IError {
    function getStackFrames(stackFrames: any[]): IStackFrame[] {
      let frames: IStackFrame[] = [];

      for (let index = 0; index < stackFrames.length; index++) {
        let frame = stackFrames[index];
        frames.push({
          name: frame.getMethodName() || frame.getFunctionName(),
          // parameters: frame.args,
          file_name: frame.getFileName(),
          line_number: frame.getLineNumber() || 0,
          column: frame.getColumnNumber() || 0,
          declaring_type: frame.getTypeName(),
          data: {
            is_native: frame.isNative() || (!!frame.filename && frame.filename[0] !== '/' && frame.filename[0] !== '.')
          }
        });
      }

      return frames;
    }

    if (!nodestacktrace) {
      throw new Error('Unable to load the stack trace library.');
    }

    let stackFrames = nodestacktrace.parse(exception) || [];
    return {
      type: exception.name,
      message: exception.message,
      stack_trace: getStackFrames(stackFrames)
    };
  }
}

                                                                                                                                                                                                                                                

export class NodeModuleCollector implements IModuleCollector {

  private initialized: boolean = false;
  private installedModules: { [id: string]: IModule } = {};

  public getModules(context: EventPluginContext): IModule[] {
    this.initialize();

    if (!require.main) {
      return [];
    }

    let modulePath = path.dirname(require.main.filename) + '/node_modules/';
    let pathLength = modulePath.length;

    let loadedKeys = Object.keys(require.cache);
    let loadedModules = {};

    loadedKeys.forEach(key => {
      let id = key.substr(pathLength);
      id = id.substr(0, id.indexOf('/'));
      loadedModules[id] = true;
    });

    return Object.keys(loadedModules)
      .map(key => this.installedModules[key])
      .filter(m => m !== undefined);
  }

  private initialize() {
    if (this.initialized) {
      return;
    }

    this.initialized = true;

    let output = child.spawnSync('npm', ['ls', '--depth=0', '--json']).stdout;

    if (!output) {
      return;
    }

    let json;
    try {
      json = JSON.parse(output.toString());
    } catch (e) {
      return;
    }

    let items = json.dependencies;
    if (!items) {
      return;
    }

    let id = 0;
    this.installedModules = {};

    Object.keys(items).forEach(key => {
      let item = items[key];
      let theModule = <IModule>{
        module_id: id++,
        name: key,
        version: item.version
      };

      this.installedModules[key] = theModule;
    });
  }
}

                                                                                                                                                                                                                             

export class NodeRequestInfoCollector implements IRequestInfoCollector {
  getRequestInfo(context: EventPluginContext): IRequestInfo {
    const REQUEST_KEY: string = '@request'; // optimization for minifier.
    if (!context.contextData[REQUEST_KEY]) {
      return null;
    }

    let exclusions = context.client.config.dataExclusions;

    // TODO: include referrer
    let request = context.contextData[REQUEST_KEY];
    let requestInfo: IRequestInfo = {
      client_ip_address: request.ip,
      user_agent: request.headers['user-agent'],
      is_secure: request.secure,
      http_method: request.method,
      host: request.hostname || request.host,
      path: request.path,
      post_data: JSON.parse(Utils.stringify(request.body, exclusions)),
      cookies: Utils.getCookies((request || {}).headers.cookie, exclusions),
      query_string: JSON.parse(Utils.stringify(request.params, exclusions))
    };

    let host = request.headers.host;
    let port: number = host && parseInt(host.slice(host.indexOf(':') + 1), 10);
    if (port > 0) {
      requestInfo.port = port;
    }

    return requestInfo;
  }
}

                                                                                                                                                                                                                                                                                                                     

export class NodeSubmissionAdapter implements ISubmissionAdapter {
  public sendRequest(request: SubmissionRequest, callback: SubmissionCallback, isAppExiting?: boolean) {
    if (isAppExiting) {
      this.sendRequestSync(request, callback);
      return;
    }

    let parsedHost = url.parse(request.serverUrl);

    let options: https.RequestOptions = {
      auth: `client:${request.apiKey}`,
      headers: {},
      hostname: parsedHost.hostname,
      method: request.method,
      port: parsedHost.port && parseInt(parsedHost.port, 10),
      path: request.path
    };

    options.headers['User-Agent'] = request.userAgent;

    if (request.method === 'POST') {
      options.headers = {
        'Content-Type': 'application/json',
        'Content-Length': request.data.length
      };
    }

    let protocol = (parsedHost.protocol === 'https' ? https : http);
    let clientRequest: http.ClientRequest = protocol.request(options, (response: http.IncomingMessage) => {
      let body = '';
      response.setEncoding('utf8');
      response.on('data', (chunk) => body += chunk);
      response.on('end', () => this.complete(response, body, response.headers, callback));
    });

    clientRequest.on('error', (error: Error) => callback(500, error.message));
    clientRequest.end(request.data);
  }

  private complete(response: http.IncomingMessage, responseBody: string, responseHeaders: Object, callback: SubmissionCallback): void {
    let message: string;
    if (response.statusCode === 0) {
      message = 'Unable to connect to server.';
    } else if (response.statusCode < 200 || response.statusCode > 299) {
      message = response.statusMessage || (<any>response).message;
    }

    callback(response.statusCode || 500, message, responseBody, responseHeaders);
  }

  private sendRequestSync(request: SubmissionRequest, callback: SubmissionCallback): void {
    let requestJson = JSON.stringify(request);
    let res = child.spawnSync(process.execPath, [require.resolve('./submitSync.js')],
      {
        input: requestJson,
        stdio: ['pipe', 'pipe', process.stderr]
      });

    let out = res.stdout.toString();
    let result = JSON.parse(out);

    callback(result.status, result.message, result.data, result.headers);
  }
}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   

const EXIT: string = 'exit';
const UNCAUGHT_EXCEPTION: string = 'uncaughtException';
const SIGINT: string = 'SIGINT';
const SIGINT_CODE: number = 2;

let defaults = Configuration.defaults;
defaults.environmentInfoCollector = new NodeEnvironmentInfoCollector();
defaults.errorParser = new NodeErrorParser();
defaults.moduleCollector = new NodeModuleCollector();
defaults.requestInfoCollector = new NodeRequestInfoCollector();
defaults.submissionAdapter = new NodeSubmissionAdapter();

function getListenerCount(emitter, event: string): number {
  if (emitter.listenerCount) {
    return emitter.listenerCount(event);
  }
  return require('events').listenerCount(emitter, event);
}

/*
 * Adding a event handler for 'uncaughtException' modifies the default
 * Node behavior, so it won't exit or log to the console. Instead,
 * we hijack the event emitter and forward the exception to the callback.
 */
function onUncaughtException(callback: (error: Error) => void) {
  let originalEmit = process.emit;

  process.emit = function(type: string, error: Error) {
    if (type === UNCAUGHT_EXCEPTION) {
      callback(error);
    }

    return originalEmit.apply(this, arguments);
  };
}

onUncaughtException(function(error: Error) {
  ExceptionlessClient.default.submitUnhandledException(error, UNCAUGHT_EXCEPTION);
});

/*
 * We cannot hijack SIGINT, so if there are no other handlers,
 * we just reproduce default Node.js behavior by exiting.
 */
process.on(SIGINT, function() {
  if (getListenerCount(process, SIGINT) <= 1) {
    process.exit(128 + SIGINT_CODE);
  }
});

process.on(EXIT, function(code: number) {
  /**
   * exit codes: https://nodejs.org/api/process.html#process_event_exit
   * From now on, only synchronous code may run. As soon as this method
   * ends, the application inevitably will exit.
   */
  function getExitCodeReason(exitCode: number): string {
    if (exitCode === 1) {
      return 'Uncaught Fatal Exception';
    }

    if (exitCode === 3) {
      return 'Internal JavaScript Parse Error';
    }

    if (exitCode === 4) {
      return 'Internal JavaScript Evaluation Failure';
    }

    if (exitCode === 5) {
      return 'Fatal Exception';
    }

    if (exitCode === 6) {
      return 'Non-function Internal Exception Handler ';
    }

    if (exitCode === 7) {
      return 'Internal Exception Handler Run-Time Failure';
    }

    if (exitCode === 8) {
      return 'Uncaught Exception';
    }

    if (exitCode === 9) {
      return 'Invalid Argument';
    }

    if (exitCode === 10) {
      return 'Internal JavaScript Run-Time Failure';
    }

    if (exitCode === 12) {
      return 'Invalid Debug Argument';
    }

    return null;
  }

  let client = ExceptionlessClient.default;
  let message = getExitCodeReason(code);

  if (message !== null) {
    client.submitLog(EXIT, message, 'Error');
  }

  client.config.queue.process(true);
  // Application will now exit.
});

(<any>Error).stackTraceLimit = Infinity;

